.music-list {
   position: absolute;
   left: 0;
   width: 100%;
   height: 100%;
   flex: 1 1 auto;
   transition: 0.15s;
   z-index: 20;
   bottom: -40px;
   display: flex;
   align-items: flex-end;
   visibility: hidden;

   &::-webkit-scrollbar {
      display: none;
   }
   &__body {
      width: 100%;
      max-height: 70%;
      border-radius: 25px 25px 0 0;
      background: var(--header);
      position: relative;
      z-index: 15;
      backdrop-filter: blur(50px);
      opacity: 0;
      visibility: hidden;
      transition: 0.15s;
   }
   &__header {
      display: flex;
      justify-content: space-between;
      padding: 20px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 6;
      border-bottom: 1px solid var(--header);
      color: var(--mainColor);
      background: var(--header);
      border-radius: 25px 25px 0 0;
   }
   &__row {
      display: flex;
      gap: 15px;
      align-items: center;
   }
   &__row span {
      padding-top: 2px;
   }
   &__list {
      display: flex;
      flex-direction: column;
      overflow-y: scroll;
      height: 50svh;
      padding: 0px 0 20px 0;
      margin-top: 66px;
      mask-image: linear-gradient(
         to top,
         rgba(0, 0, 0, 0),
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         rgba(0, 0, 0, 0)
      );
      &::-webkit-scrollbar {
         display: none;
      }
   }
   &__line {
      display: flex;
      justify-content: space-between;
      padding: 15px 20px;
      cursor: pointer;
      transition: background 0.15s;
      color: var(--black-p);
   }
   &__line:hover {
      background: #ffffff10;
   }
   &__line.playing {
      background: var(--header);
      pointer-events: none;
   }
   &__item p {
      opacity: 0.8;
      font-size: 12px;
      letter-spacing: 1.1px;
      color: var(--black-p);
   }
   &__icon {
      cursor: pointer;
      height: 25px;
      width: 25px;
      div {
         height: 25px;
         width: 25px;
      }
      svg {
         height: 25px;
         width: 25px;
      }
   }
   &__icon:nth-child(2) {
      height: 20px;
      width: 20px;
      div {
         height: 20px;
         width: 20px;
      }
      svg {
         height: 20px;
         width: 20px;
      }
   }
}
.music-list.open {
   bottom: 0px;
   visibility: visible;
}
.music-list.open .music-list__body {
   visibility: visible;
   opacity: 1;
   bottom: 0px;
}
