.song-item {
   transition: 0.5s;
   &__grid {
      display: none;
   }
   &__wrapper {
      height: 100%;
      aspect-ratio: 1/1;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;
   }
   &__img {
      width: 80%;
      height: 80%;
      aspect-ratio: 1/1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
      background: #000;
      border-radius: 20px;
      z-index: 4;
   }
   &__bg {
      height: 80%;
      width: 80%;
      display: grid;
      place-items: center;
      grid-template-rows: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr;
      z-index: -1;
      filter: contrast(5) blur(80px);
      opacity: 0.6;
      transition: 0.5s;
      animation-name: fade;
      animation-duration: 10s;
      animation-timing-function: cubic-bezier(0.74, -0.01, 0.26, 0.73);
      animation-iteration-count: infinite; /* Repetă animația la nesfârșit */
   }
}
.color-box {
   width: 100%;
   height: 100%;
   transition: 0.5s;
}
@keyframes fade {
   0%,
   100% {
      opacity: 0.6;
      transform: scale(1) skew(-5deg) rotate(0deg);
   }
   50% {
      opacity: 0.3;
      transform: scale(1.1) skew(5deg) rotate(-10deg);
   }
}
